//fixed header
$(window).scroll(function(){
	if ($(window).scrollTop() > 1) {
		$('#header-fixed').addClass('scrolled');
	}
	else {
		$('#header-fixed').removeClass('scrolled');
	}
});

$(document).ready(function(){
	$(function(){
	 if ($(window).scrollTop()>="250") $("#arrow-up-scroll").fadeIn("slow")
	 $(window).scroll(function(){
	  if ($(window).scrollTop()<="250") $("#arrow-up-scroll").fadeOut("slow")
	   else $("#arrow-up-scroll").fadeIn("slow")
	 });

	 if ($(window).scrollTop()<=$(document).height()-"999") $("#arrow-down-scroll").fadeIn("slow")
	 $(window).scroll(function(){
	  if ($(window).scrollTop()>=$(document).height()-"999") $("#arrow-down-scroll").fadeOut("slow")
	   else $("#arrow-down-scroll").fadeIn("slow")
	 });

	 $("#arrow-up-scroll").click(function(){$("html,body").animate({scrollTop:0},"slow")})
	 $("#arrow-down-scroll").click(function(){$("html,body").animate({scrollTop:$(document).height()},"slow")})
	});
});

